/* Card Shop */
.card--shop {
  margin-bottom: 1rem;
  margin-top: 1rem;
  .card-head {
    position: relative;
    .shop-logo {
      border: 2px solid $orange1;
      border-radius: 50%;
      width: 35%;
      position: absolute;
      bottom: -10px;
      right: 15px;
      img {
        width: 100%;
        border-radius: 50%;
      }
    }
    .rate {
      position: absolute;
      left: 10px;
      bottom: 14px;
    }
  }

  .card-body {
    background-color: $gray3;
    border-top: 1.5px solid $orange1;
    .card-body__title {
      i {
        color: $orange1;
        font-size: 12px;
      }
      span {
        color: $gray2;
        font-size: 1rem;
        font-weight: bold;
      }
    }
    .card-body__addess,
    .card-body__phone {
      text-decoration: none;
      p {
        i {
          color: $orange1;
          font-size: 12px;
        }
        span {
          color: $gray2;
          font-size: 0.7rem;
          font-weight: bold;
        }
      }
    }

    .social {
      display: flex;
      margin: 3px 0 0 0;
      li {
        &:not(:last-of-type) {
          margin-inline-end: 10px;
        }
        i {
          font-size: 25px;
          color: $gray2;

          &:hover {
            color: $orange1;
            transition: color 0.3s ease-in-out;
          }
        }
      }
    }
  }
}
/* End Card Shop */
/* Card Shop */
.card--offer {
  margin-bottom: 1rem;
  margin-top: 1rem;
  .card-head {
    position: relative;

    .sale-box {
      width: 100px;
      top: -8px;
      left: -7px;
      position: absolute;
      img {
        width: 100%;
      }
    }
  }

  .card-body {
    background-color: $gray3;
    border-top: 1.5px solid $orange1;
    .card-body__title {
      span {
        color: $gray2;
        font-size: 1rem;
        font-weight: bold;
      }
      .under-line {
        border-bottom: 2px solid $gray2;
      }
    }
    .card-body__addess,
    .card-body__phone {
      text-decoration: none;
      p {
        i {
          color: $orange1;
        }
        span {
          color: $gray2;
          font-size: 0.7rem;
          font-weight: bold;
        }
      }
    }
    .offer-container {
      text-align: left;

      .take-offer {
        text-decoration: none;
        color: $gray2;
        background-color: $orange1;
        padding: 2px 7px;
        font-size: 0.8rem;
        margin-top: 10px;
        display: inline-block;
        &:hover {
          color: $orange1;
          background-color: $gray2;
          transition: all 0.3s ease-in-out;
        }
      }
    }
  }
}
/* End Card Shop */
/* Card Events */
.card--events {
  margin-bottom: 1rem;
  margin-top: 1rem;
  .card-head {
    position: relative;
    .event-date {
      position: absolute;
      width: 85px;
      top: 0;
      left: 0;
      img {
        width: 100%;
      }

      .event-date__m {
        position: absolute;
        top: 21px;
        left: 17px;
        font-size: 12px;
        color: #fff;
        font-weight: bold;
      }
      .event-date__d {
        position: absolute;
        top: 43px;
        left: 34px;
        font-size: 20px;
        color: #000;
        font-weight: bold;
      }
    }
  }

  .card-body {
    background-color: $gray3;
