    .card-body__addess,
    .card-body__phone {
      text-decoration: none;
      p {
        i {
          color: $orange1;
        }
        span {
          color: $gray2;
          font-size: 0.7rem;
          font-weight: bold;
        }
      }
    }
    .offer-container {
      text-align: left;

      .take-offer {
        text-decoration: none;
        color: $gray2;
        background-color: $orange1;
        padding: 2px 7px;
        font-size: 0.8rem;
        margin-top: 10px;
        display: inline-block;
        &:hover {
          color: $orange1;
          background-color: $gray2;
          transition: all 0.3s ease-in-out;
        }
      }
    }
  }
}
/* End Card Events */

/* Detaitls Card  */
.details-card {
  border-radius: 15px;
  padding: 15px;

  .card__text p {
    font-size: 1.4rem;
    color: $gray2;

    i {
      color: $orange1;
      font-size: 1rem;
    }
  }
  .card__img {
    max-width: 150px;
    img {
      width: 100%;
    }
  }
}

/* End Detaitls Card  */

/* description */
.description {
  .description__head {
  }
  .description__body {
    border: 1px solid $gray2;
    border-radius: 15px 0 15px 15px;
    overflow: hidden;
    p {
      padding: 15px;
      background-color: $gray3;
      color: $gray2;
      margin-bottom: 0;
      font-size: 16px;
    }
  }
}
/* // description */

/* terms */

.terms {
  .terms__body {
    .terms__list {
      .list__item {
        .item__text {
          margin: 0;
          padding-top: 10px;
          
          &::before {
            content: "";
            display: inline-block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: $orange1;
            vertical-align:-2px;
           
          }
        }
      }
    }
  }
}

/* // terms */
