.side-carsouel {
  .swiper-container {
    width: 100%;
    height: 200px;
    margin-left: auto;
    margin-right: auto;
  }
  .swiper-slide {
    background-size: cover;
    background-position: center;
  }
  .gallery-top {
    height: 70%;
    width: 100%;
    max-height: 350px;
    border-radius: 15px;
    border: 2px solid $orange1;
  }
  .gallery-thumbs {
    height: 15%;
    box-sizing: border-box;
    padding: 10px 0;
  }
  .gallery-thumbs .swiper-slide {
    height: 100%;
    opacity: 0.4;
    border-radius: 15px;
  }
  .gallery-thumbs .swiper-slide-thumb-active {
    opacity: 1;
    border: 2px solid $orange1;
    border-radius: 15px;
  }
}

@media screen and #{$maxMobile} {
  .side-carsouel {
    min-height: 450px;
  }
}
